import {profileAPI, usersAPI} from "../api/api";
import {follow, toggleIsFetching} from "./users-reducer";
import {togglefollowingInProgress} from "./myusers-reducer";

const ADD_POST = 'ADD-POST';
const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT';
const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_STATUS = 'SET_STATUS'

let initialState = {
    posts: [
        {id: 1, message: 'Hi, how are you?', likesCount: 12},
        {id: 2, message: 'It\'s my first post', likesCount: 11},
        {id: 3, message: 'Blabla', likesCount: 11},
        {id: 4, message: 'Dada', likesCount: 11}
    ],
    newPostText: 'it-kamasutra.com',
    profile: null,
    status: ""
};

const profileReducer = (state = initialState, action) => {

    switch(action.type) {
        case ADD_POST: {
            let newPost = {
                id: 5,
                message: state.newPostText,
                likesCount: 0
            };
            return {
                ...state,
                posts: [...state.posts, newPost],
                newPostText: ''
            };
        }
        case UPDATE_NEW_POST_TEXT: {
            return {
                ...state,
                newPostText: action.newText
            }
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        case SET_USER_PROFILE: {
            return {...state, profile: action.profile}
        }

        default:
            return state;
    }
}


export const addPostActionCreator = () => ({type: ADD_POST})
export const updateNewPostTextActionCreator = (text) =>
    ({type: UPDATE_NEW_POST_TEXT, newText: text })
export const setUserProfile = (profile) =>
    ({type: SET_USER_PROFILE, profile })
export const setStatus = (status) =>
    ({type: SET_STATUS, status })




export const getProfileThunk = (userId) => {
    return (dispatch) => {
        dispatch(toggleIsFetching(true));
        profileAPI.getProfile(userId)
            .then(response => {
                dispatch(setUserProfile(response.data))
                dispatch(toggleIsFetching(false));
            })
    }
}

export const getStatusThunk = (userId) => {
    return (dispatch) => {
        profileAPI.getProfileStatus(userId)
            .then(response => {
                dispatch(setStatus(response.data))
            })
    }
}

export const updateStatusThunk = (status) => {
    return (dispatch) => {
        profileAPI.putProfileStatus(status)
            .then(response => {
                if (response.data.resultCode == 1) alert("Что-то пошло не так.")
                else dispatch(setStatus(status))
            })


    }
}






export default profileReducer;