import React from 'react';
import s from './ProfileInfo.module.css';
import Preloader2 from "../../Preloader/Preloader";
import ProfileStatus from "./ProfileStatus";
import {NavLink} from "react-router-dom";


const ProfileInfo = (props) => {

    if (!props.profile) {
        return <Preloader2 />
    }

    return (
        <div>

            <img className={s.imageStyle}
                 src='https://upload.wikimedia.org/wikipedia/commons/thumb/2/2c/The_social_network.svg/1280px-The_social_network.svg.png'/>

            <div className={s.descriptionBlock}>
                <img src={props.profile.photos.large}/>
                <div><ProfileStatus status={props.status} updateStatusThunk={props.updateStatusThunk}/></div>
                <div>
                    <div><span className={s.profiledata}>Номер пользователя:</span> {props.profile.userId}</div>
                </div>
                <div>
                    <div><span className={s.profiledata}>Вконтакте:</span> <a href={props.profile.contacts.vk}>{props.profile.contacts.vk}</a></div>
                </div>
                <div>
                    <div><span className={s.profiledata}>Инстаграмм:</span> <a href={props.profile.contacts.instagram}>{props.profile.contacts.instagram}</a></div>
                </div>
                <div>
                    <div><span className={s.profiledata}>Своя страница:</span> <a href={props.profile.contacts.mainLink}>{props.profile.contacts.mainLink}</a></div>
                </div>
                <div>
                    <div><span className={s.profiledata}>Веб сайт:</span> <a href={props.profile.contacts.website}>{props.profile.contacts.website}</a></div>
                </div>
                <div>
                    <div><span className={s.profiledata}>YouTube:</span> <a href={props.profile.contacts.youtube}>{props.profile.contacts.youtube}</a></div>
                </div>
                <div>
                    <div><span className={s.profiledata}>Твиттер:</span> <a href={props.profile.contacts.twitter}>{props.profile.contacts.twitter}</a></div>
                </div>
                <div>
                    <div><span className={s.profiledata}>Facebook:</span><a href={props.profile.contacts.facebook}>{props.profile.contacts.facebook}</a></div>
                </div>
                <div>
                    <div><span className={s.profiledata}>Гитхаб:</span> <a href={props.profile.contacts.github}>{props.profile.contacts.github}</a></div>
                </div>
                <div>
                    <div><span className={s.profiledata}>Поиск работы:</span> {props.profile.lookingForAJobDescription}</div>
                </div>
                <div>
                    <div><span className={s.profiledata}>Полное имя:</span> {props.profile.fullName}</div>
                </div>
            </div>
        </div>
    )
}
//userId: required(integer)
// lookingForAJob: required(boolean)
// lookingForAJobDescription: required(string)
// fullName: required(string)
// contacts: required(object)
// github: required(string)
// vk: required(string)
// facebook: required(string)
// instagram: required(string)
// twitter: required(string)
// website: required(string)
// youtube: required(string)
// mainLink: required(string)

export default ProfileInfo;