import React from 'react';
import s from './Header.module.css';
import headerimg from '../../img/social-venture-network-logo.png'
import {NavLink} from "react-router-dom";
import userlogo from '../../img/userlogo.jpg'


const Header = (props) => {
    return <header className={s.header}>

        <span>
            {props.isAuth ? <span className={s.exitspan}> <button className={s.pagin} onClick={props.logout}>Выйти</button></span> : ""}
            <img src={headerimg}/>
            <span className={s.loginblock}>
                <img src={userlogo}/> {props.isAuth ? props.login :
                <NavLink to={'/login'}>Login</NavLink>}
            </span>


        </span>
    </header>
}

export default Header;