import {NavLink} from "react-router-dom";
import React from "react";
import userPhoto from '../../img/userlogo.jpg'
import green from '../../img/greengalka.png'
import red from '../../img/redkrest.png'
import styles from './users.module.css'


let UsersList = (props) => {



    return <div> {props.users.map(u =>
            <div className={styles.user}>
                <div className={styles.userName}>{u.name}</div>
                <div><NavLink to={'/profile/' + u.id}>
                    <img className={styles.userPhoto}
                         src={u.photos.small != null ? u.photos.small : userPhoto}/></NavLink>
                </div>
                {u.status
                    ? <div><img className={styles.greenorred} src={green}/></div>
                    : <div><img className={styles.greenorred} src={red}/></div>}
                <div>№:{u.id}</div>
                <div>
                    {u.followed
                        ? <div className={styles.followed}>У вас в друзьях</div>
                        : <div>Не в друзьях</div>}</div>
                <div>
                    {u.followed
                        ? <button className={styles.iksweb} disabled={props.followingInProgress.some(id => id === u.id)}
                                  onClick={() => {
                                      props.unfollowThunk(u.id)
                                  }}>Удалить из друзей</button>
                        : <button className={styles.iksweb} disabled={props.followingInProgress.some(id => id === u.id)}
                                  onClick={() => {
                                      props.followThunk(u.id)
                                  }}>Добавить в друзья</button>}
                </div>
            </div>)}

    </div>

}
export default UsersList