import React from 'react';
import s from './ProfileInfo.module.css';

class ProfileStatus extends React.Component {

    state = {
        editMode: false,
        status: this.props.status
    }

    activateEditMode =()=> {
        this.setState({
            editMode: true
        })

    }
    deactivateEditMode =()=> {
        this.setState({
            editMode: false
        })
        this.props.updateStatusThunk(this.state.status)

    }
    onStatusChange = (e) => {
        this.setState({
            status: e.currentTarget.value
        })
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.status !== this.props.status) {
            this.setState({status: this.props.status})
        }
    }

    render() {
       return ( <div>
           {!this.state.editMode && <span onClick={this.activateEditMode}>{this.props.status || "...нет статуса..."}<br/></span>}
           {this.state.editMode && <div><span className={s.statuschange}>Статус:</span> <input onChange={this.onStatusChange} autoFocus={true} onBlur={this.deactivateEditMode} value={this.state.status}/></div>}
        <br/>
       </div>
       )}
}


export default ProfileStatus;