import React from 'react';
import s from './Login.module.css';
import {NavLink, Redirect} from "react-router-dom";
import {loginThunk} from "../../redux/auth-reducer";
import {Field, reduxForm} from "redux-form";
import {connect} from "react-redux";
import {maxLength10, maxLength30, maxLength50, required} from "../../validators/validators";
import {Input} from "../../formcontrols/formControl";


const LoginForm = (props) => {



    return <div>
        <div className={s.loginpage}>
            <div className={s.form}><div className={s.mainlogin}>Вход</div>
                <form onSubmit={props.handleSubmit}>
                    <span><Field
                        component={Input}
                        name={"email"}
                        placeholder={"Почта"}
                    validate={[required,maxLength50]}/>
                    </span>
                    <span><Field
                        component={Input}
                        name={"password"}
                        placeholder={"Пароль"}
                        type={"password"}
                        validate={[required, maxLength10]}/></span>

                    <span className={s.rememberme}>Запомнить меня<Field component={"input"} name={"rememberMe"} type={"checkbox"}/></span>
                    <div><button>Войти</button></div>
                    <p className={s.message}>Еще не зарегистрирован? <NavLink to="/registration">Создать аккаунт</NavLink></p>
                </form>
            </div>
        </div>
    </div>
}


const ReduxLogin = reduxForm({ form: "login" })(LoginForm)

const Login = (props) => {
    const onSubmit = (formData) => {
        props.loginThunk(formData.email, formData.password, formData.rememberMe)
    }
    if (props.isAuth) {
        return <Redirect to={'/profile'} />
    }
    return <ReduxLogin onSubmit={onSubmit}/>
}

let mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})


export default connect (mapStateToProps, {loginThunk})(Login);