import React from 'react';
import Profile from "./Profile";
import {connect} from "react-redux";
import {getProfileThunk, getStatusThunk, updateStatusThunk} from "../../redux/profile-reducer";
import {withRouter} from "react-router-dom";
import Preloader2 from "../Preloader/Preloader2";
import {compose} from "redux";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";

class ProfileContainer extends React.Component {

    componentDidMount() {
        let userId = this.props.match.params.userId
        if (!userId) {
            userId = this.props.authUserId
            // if (!userId) {this.props.history.push("/dialogs")} способ програмнного редиректа
        }
        this.props.getProfileThunk(userId)
        this.props.getStatusThunk(userId)
    }


    render () {
        return <>
            {this.props.isFetching ? <Preloader2/> : <Profile {...this.props} profile={this.props.profile} status={this.props.status} updateStatusThunk={this.props.updateStatusThunk}/> }
    </>
    }
}

let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    isFetching: state.usersPage.isFetching,
    status: state.profilePage.status,
    updateStatusThunk: state.profilePage.updateStatusThunk,
    authUserId: state.auth.userId,
    isAuth: state.auth.isAuth

})



export default compose(
   withAuthRedirect,
    withRouter,
    connect(mapStateToProps, {getProfileThunk, getStatusThunk, updateStatusThunk})
) (ProfileContainer);